<template>
	<view class="container">
		<view class="header" :style="headerStyle">
			<view class="status-bar"></view>
			<view class="nav-content" :style="navStyle">
				<view class="location">
					<uni-icons type="location" size="28" color="#fff"></uni-icons>
					<text class="city">郑州市</text>
				</view>
				<view class="search-box">
					<uni-search-bar 
					radius="100" 
					placeholder="搜索宠物服务" 
					clearButton="none" 
					cancelButton="none"
					style="width: 100%"
					>
					</uni-search-bar>
				</view>
			</view>
		</view>
	</view>
</template>

<script lang="ts" setup>
import {onLoad} from "@dcloudio/uni-app"
import {ref,computed} from "vue"

const menuButtonInfo  = ref(null)

onLoad(() =>{

	menuButtonInfo.value = uni.getMenuButtonBoundingClientRect()	

	






})

//自适应高度
const headerStyle = computed(() => {
	let style = {
		height:"200px"
	}

	if(menuButtonInfo.value){
		style={
			height:`${menuButtonInfo.value.top+menuButtonInfo.value.height+20}px`,
		}
	}







	return style
})
const navStyle = computed(() => {
	let style = {
		top:"0px",
		height:"44px"
	}

	if(menuButtonInfo.value){
		style={
			top:`${menuButtonInfo.value.top}px`,
			height:`${menuButtonInfo.value.height}px`,
		}
	}








	return style
})

</script>

<style lang="scss" scoped>
.container{
	background-color: #f7f7f7;
	.header{
		background-color: $uni-color-primary;
		.status-bar{
			width:100%;
			height: var(--status-bar-height);
		}
		.nav-content{
			display: flex;
			align-items: center;
			padding: 0 30rpx;
			position: fixed;
			left:0;
			right:0;
			.location{
				display: flex;
				align-items: center;
				margin-right: 10rpx;
				.city{
					color:#fff;
					font-size: 28rpx;
					width:120rpx;
					white-space: normal;
					overflow: hidden;
					text-overflow: ellipsis;
				}
			}
			.search-box{
				width:360rpx;
			}
		}
	}
}
</style>
